// ? Componentes de react
import React from 'react';
import Layout from './layout';
// ~ Componentes de Gatsby
import { GatsbyImage } from 'gatsby-plugin-image';
// ¬ Importar para manejar el query hecho en GraphiQL
import { graphql } from 'gatsby';
// ¿ Estilos
import { css } from '@emotion/react';

/*
    ° Este query obtiene una variable ($slug) la cual viene desde gatsby-node.js
    ° String! -> significa que la variable ($slug) es de tipo String y es obligatoria (!)
    ° Se filtran los datos por la variable ($slug) que sea igual a (slug) en el registro consultado
    ° No es necesario definir la variable en otro lado.
    ° Desde GraphiQL se puede construir esta consulta usando el apartado de (Query Variables)
*/
export const query = graphql`
  query ($slug: String!) {
    allDatoCmsHabitacion(filter: {slug: {eq: $slug}}) {
      nodes {
        titulo
        contenido
        imagen {
          gatsbyImageData(layout: FULL_WIDTH)
        }
      }
    }
  }
`
// Se aplica destructuring en lugar de usar (data.allDatoCms.nodes[0])
const HabitacionTemplate = ( { data: { allDatoCmsHabitacion: { nodes } } } ) => {

  const { titulo, contenido, imagen } = nodes[ 0 ];

  return (
    <Layout>
      <main
        css={ css`
        margin: 0 auto;
        max-width: 120rem;
        width: 95%;
        `}
      >
        <h1
          css={ css`
          text-align: center;
          margin-top: 4rem;
          `}
        >{ titulo }</h1>

        <p>{ contenido }</p>

        <GatsbyImage image={ imagen.gatsbyImageData } alt={ titulo } />
      </main>
    </Layout>
  );
}



export default HabitacionTemplate;